.add-post-modal--bg{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.85);
    width: 100%;
    height: 100%;
    top: 0;
}

.add-post-modal{
    position: relative;
    width: 56rem;
    height: 60.2rem;
    border-radius: 1.5rem;
    background-color: #fff;
}

.add-post-modal--header{
    border-bottom: 1px solid lightgray;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 4.22rem;

    p{
        font-size: 1.4rem;
        font-weight: 700;
    }

    .next-post-step{
        cursor: pointer;
        color: #0095f6;
    }

    .arrow-left{
        cursor: pointer;
    }
}

.add-post-modal--body{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 55.98rem;

    p{
        display: block;
        font-size: 2.4rem;
        font-weight: 200;
    }

    .full-width{
        width: 100%;
        height: 55.98rem;
        border-bottom-left-radius: 1.5rem;
        border-bottom-right-radius: 1.5rem;
    }
}

